<template lang="pug">
  div(id="animation-loading")
    Animation(:maskColor="maskColor", :pointColor="pointColor", :shapeType="shapeType", appendNode="#animation-loading1", :isShow="true")
    v-md-editor.github-markdown-body(:value="text" mode="preview")
    div(style="margin: 0 30px;")
      h4(@click="switchType(1)") 示例1
      //h4(@click="switchType(2)") 示例2
    div(id="animation-loading1").demo-content
       div(style="padding: 0 30px")
</template>

<script>
import Animation from "@/components/animation/Animation";

export default {
  components: {
    Animation,
  },
  data() {
    return {
      maskColor: '',
      pointColor: '',
      shapeType: 'square',
      text: `
  ### 加载动画
  #### Animation
  - \`maskColor\`：动画模块遮罩的颜色，默认值\`rgba(10,10,10,0.1);\`，可选格式为\`#444/rgb(32, 82, 149)/red\`，字符串类型
  - \`pointColor\`：动画模块的颜色，默认值\`rgba(32, 82, 149,0.9)\`，可选格式为\`#444/rgb(32, 82, 149)/red\`，字符串类型
  - \`shapeType\`：动画模块的形状，默认值\`square\`，可选值为\`square/circle\`，字符串类型
  - \`isShow\`：是否显示，默认值\`false\`，可选值为\`true/false\`，字符串或者布尔类型
  - \`appendNode\`：需要附加加载框的接口，默认值\`body\`
  > 默认是整个页面都是灰色遮罩，需要具体到某个节点的，将节点添加id，appendNode设置这个id
  > **注意1**，灰色遮罩是依赖\`position: absolute\`定位的，到具体节点的话，请css设置\`position: relative\`
  > **注意2**，灰色遮罩宽度是\`width: 100%\`，设置id的节点使用padding左右边距会偏移。由于是相对定位和绝对定位，box-sizing: border-box设置也无效。解决办法：设置id的节点不使用padding左右边距，需要左右边距的，在子div设置即可

  > Animation声明位置，没有强制要求，不语法报错即可
  > Animation一个页面不支持有多个加载动画
  #### 示例
  \`\`\`pug
  // html-pug
  div(id="animation-loading")
    Animation(pointColor="", shapeType="square", appendNode="#animation-loading1", :isShow="true")
    v-md-editor.github-markdown-body(:value="text" mode="preview")
    div(id="animation-loading1").demo-content
       div(style="padding: 0 30px") 这是需要设置加载框的节点
  \`\`\`
  \`\`\`css
  // css
  .demo-content {
    position: relative;
    border: 1px solid rgba(244,244,244, 0.9);
    padding: 12px 0px;
    margin: 0 30px;
    height: 100px;
  }
  \`\`\`
      `,
    };
  },
  methods: {
    switchType(ret) {
      if (ret == 1) {
        this.maskColor = '';
        this.pointColor = '';
        this.shapeType = 'square';
      } else if (ret == 2) {
        this.maskColor = '#fff';
        this.pointColor = '#FD8A8A';
        this.shapeType = 'rotateSquare';
      }
    }
  },
}
</script>

<style scoped>
/deep/ .github-markdown-body {
  padding: 12px 16px 0px;
}
.demo-content {
  position: relative;
  border: 1px solid rgba(244,244,244, 0.9);
  padding: 12px 0px;
  margin: 0 30px 40px 30px;
  height: 200px;
}
.file-text {
  color: #657cc4;
}
h4 {
  margin-bottom: 4px;
  margin-right: 4px;
  padding: 2px 12px;
  box-shadow: 0px 0px 1px rgba(80,80,80,0.3);
  cursor: pointer;
  display: inline-block;
}
h4:hover {
  background: rgba(80,80,80,0.3);
}
</style>
