  #### easy-table基础用法
  - \`data\`：表格数据，对象数组格式，\`必须属性\`
  - \`columns\`：表格字段，对象数组格式，\`必须属性\`
  ##### columns内对象属性和方法
  - \`name\`：获取数据的接口字段。字符串
  - \`title\`：表头展示的字段。字符串
  - \`sortable\`：是否可排序。布尔值默认false
  - \`fixed\`：是否固定在左侧或者右测。字符串可选值\`left/right\`，默认不设置
  - \`showOverflowTooltip\`：在单元格内容过长时候显示\`...\`，鼠标悬浮显示Tooltip
  - \`align\`：列表数据文案位置，字符串默认'left'，可选值：\`left/center/right\`
  - \`headerAlign\`：表头文案位置，字符串默认'left'，可选值：\`left/center/right\`
  - \`show\`：列是否显示，使用场景：有相似的页面列表，只是增减了某个列，通过这个属性可以复用。function或者布尔值
  - \`formatter\`：自定义展示内容。前三个入参row, cell, value分别是当前行对象，当前格对象，当前格数值。function
  - \`buttons\`：操作列，提供交互操作。对象数组
  > **buttons**对象属性如下
  > \`name\`：交互按钮名称，字符串
  > \`click\`：点击触发方法，两个方法入参，第一个是index，第二个是当前行row数据。function
