  - \`type\`：自定义单元格类型，可选值：\`label/operate\`
  - \`labels/buttons\`：type为label时候。可设置是否自定义单元格内容。对象数组类型，可选值：\`labels/buttons\`
  > **labels**对象属性如下
  > \`name\`：单元格内容，字符串
